      </a>

      <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

      <b-collapse id="nav-collapse" is-nav>
        <b-navbar-nav>
          <b-nav-item :to="'./index'">Inicio</b-nav-item>
          <b-nav-item to="./about">Conocenos</b-nav-item>
          <b-nav-item to="./products">Productos</b-nav-item>
          <b-nav-item to="./rep">Representaciones</b-nav-item>
          <b-nav-item to="./clients">Clientes</b-nav-item>
          <b-nav-item to="./contact">Contáctenos</b-nav-item>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
  </div>
</template>

<script>
export default {
  props: ["name"],
  data() {
    return {
      pais: ""
    };
  },
  created() {
    if (this.name == "ve") this.pais = "Venezuela";
    else if (this.name == "co") this.pais = "Colombia";
    else if (this.name == "pa") this.pais = "Panama";
    else console.log("error de pais");
    console.log(this.name);
  },
  updated() {
    this.pais = this.$route.country;
  }
};
</script>

