import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Index from "../views/Index.vue";
import About from "../views/About.vue";
import Clients from "../views/Clients.vue";
import Client from "../views/Client.vue";
import Contact from "../views/Contact.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/:country/index",
    name: "index",
    component: Index,
    props: true
  },
  {
    path: "/:country/contact",
    name: "contact",
    component: Contact,
    props: true
  },
  {
    path: "/:country/about",
    name: "about",
    component: About,
    props: true
  },
  {
    path: "/:country/clients",
    name: "clients",
    component: Clients,
    props: true
  },
  {
    path: "/:country/clients/:id",
    name: "client",
    component: Client,
    props: true
  },
  {
    path: "/:country/products/",
    name: "client",
    component: Client,
    props: true
  }
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes
});

export default router;
