<template>
  <div class="home">
    <b-container>
      <div class="divider div-transparent"></div>
      <b-row align-h="center">
        <b-col cols="12" md="1" align-self="center">
          <b-img src="\img\bfgp.png" alt="logo de BFPG" height="100%" />
        </b-col>
        <b-col cols="12" md="3" align-self="center">
          <h1>{{ name }}</h1>
        </b-col>
      </b-row>
      <div class="divider div-transparent div-arrow-down"></div>
      <b-row align-h="center">
        <b-col class="padder">
          <b-carousel
            id="carousel-1"
            :interval="4000"
            controls
            indicators
            background="#ababab"
            img-width="1024"
            img-height="480"
            style="text-shadow: 1px 1px 2px #333;"
            fluid
          >
            <!-- Text slides with image -->
            <b-carousel-slide
              caption="First slide"
              text="Nulla vitae elit libero, a pharetra augue mollis interdum."
              img-src="https://picsum.photos/1024/480/?image=52"
            ></b-carousel-slide>

            <!-- Slides with custom text -->
            <b-carousel-slide
              img-src="https://picsum.photos/1024/480/?image=54"
            >
              <h1>Hello world!</h1>
            </b-carousel-slide>

            <!-- Slides with image only -->
            <b-carousel-slide
              img-src="https://picsum.photos/1024/480/?image=58"
            ></b-carousel-slide>

            <!-- Slides with img slot -->
            <!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment -->
            <b-carousel-slide>
              <template v-slot:img>
                <img
                  class="d-block img-fluid w-100"
                  width="1024"
                  height="360"
                  src="https://picsum.photos/1024/480/?image=55"
                  alt="image slot"
                />
              </template>
            </b-carousel-slide>
          </b-carousel>
        </b-col>
      </b-row>
    </b-container>
    <b-container>
      <b-row align-h="center">
        <card
          v-for="info in displaying"
          :key="info.id"
          :name="info.name"
          :content="info.content"
        ></card>
        <div class="little-pad">
          <b-card
            header-tag="header"
            img-alt="job-info"
            img-top
            tag="article"
            no-body
            style="max-width:22rem;min-height:32rem;max-height:32rem;min-width:22rem"
            class="mb-2"
          >
            <b-card-body>
              <b-card-title>
                <h3><strong>Boletin Informativo</strong></h3>
              </b-card-title>
              <b-card-img
                src="img/iconmonstr-email-2.svg"
                class="rounded-0"
                height="130"
              ></b-card-img>

              <b-form>
                <b-form-group
                  id="input-group-1"
                  label="Dirección de correo :"
                  label-for="input-1"
                >
                  <b-form-input
                    id="input-1"
                    v-model="form.email"
                    type="email"
                    required
                    placeholder="E-mail"
                  ></b-form-input>
                </b-form-group>

                <b-form-group
                  id="input-group-2"
                  label="Nombre :"
                  label-for="input-2"
                >
                  <b-form-input
                    id="input-2"
                    v-model="form.name"
                    required
                    placeholder="Nombre"
                  ></b-form-input>
                </b-form-group>
              </b-form>
            </b-card-body>
            <div class="mt-auto padder">
              <button type="button" class="btn btn-lg btn-block btn-primary">
                Subscribirse
              </button>
            </div>
          </b-card>
        </div>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import cards from "@/components/cards.vue";
export default {
  props: ["country"],
  data() {
    return {
      currentPage: 1,
      rows: 1,
      perPage: 2,
      infos: [],
      displaying: [],
      sliding: null,
      form: {
        email: "",
        name: "",
        food: null,
        checked: []
      },
      name: ""
    };
  },

  name: "Home",
  components: {
    card: cards
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        const res = await fetch("/" + this.country + "/info.json");
        const val = await res.json();
        this.infos = val;
        this.name = this.infos[0].pais;
        this.displaying = val.slice(1, 3);
        this.rows = this.infos.length;
      } catch (error) {
        console.log(error);
      }
    },
    paginate(currentPage) {
      const start = (currentPage - 1) * this.perPage;
      this.displaying = this.infos.slice(start, start + 2);
    }
  },
  computed: {
    username() {
      return this.hi.params.country;
    }
  }
};
</script>
