<template>
  <div id="app">
    <navigation v-show="toggle" :name="pais"></navigation>
    <router-view @updateStatus="updateStatus" class="content" />
    <footer v-show="toggle" class="footer"><pie /></footer>
  </div>
</template>

<script>
import navigation from "@/components/navigation.vue";
import footer from "@/components/footer.vue";
export default {
  components: {
    navigation: navigation,
    pie: footer
  },
  data() {
    return {
      pais: "",
      toggle: false
    };
  },
  methods: {
    updateStatus(status) {
      this.toggle = !this.toggle;
      this.$router.push(status);
    }
  },
  created() {
    this.toggle = false;
    this.pais = this.$route.params.country;
  },
  beforeUpdate() {
    this.pais = this.$route.params.country;
  }
};
</script>
